.pokemonInfo{
	font-family: inherit;
	height: 400px;
	width: 300px;
	background-color:#81c784;
	position: fixed;
	top: 20%;
	right: 4%;
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 2px solid #000;
}

.name{
	font-size: 23px;
}

.PokemonTitle{
	font-family: inherit;
	display: flex;
	justify-content: center;
	font-size: 24px;
	letter-spacing: 2px;
}

table{
	width: 250px;
}
td{
	border: 1px solid #00b248;
	padding: 2px;
}
table td:nth-child(even){
	text-align: right;
}
table td:nth-child(odd){
	text-align: left;
	width: 120px;
}
.type{
 border: 1px solid #fff;
 margin: 0 2px;
 padding: 1px;
}

@media (max-width:  600px){
	.pokemonInfo{
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
   }
   .closeBtn{
    position: absolute;
     right: 20px;
    top: 15px;
	width: 5px;
	height: 30px;
	transform: rotate(45deg);
	background-color: #000;
	cursor: pointer;
   }
   .closeBtn::before{
   	content: "";
   	position: absolute; 
    right: 0px;
    top: 0px;
	width: 5px;
	height: 30px;
	transform: rotate(-85deg);
	background-color: #000;
	cursor: pointer;
   }
}