 /*PRE-LOADER*/

 .preloader_wrapper{
 	position: fixed;
 	top: 0px;
 	left: 0px;
 	width: 100%;
 	height: 100%;
 	background-color: rgba(0,0,0,0.4);

display: flex;
justify-content: center;
align-items: center;
 }





.preloader_box{
 height: 70px;
 width: 70px;
 border-radius: 50%;
 border: rgba(255,255,255,0.6) 8px solid;
 border-top: #1ffcff 8px solid;

animation: preloader 2s infinite;

}



@keyframes preloader {
	0%{
		transform: rotate(0);

	}
	
	
	100%{
		transform: rotate(360deg);

	}
	
}




