.card{
	border: 2px solid #000;
	padding: 5px 0px 20px 0px;
    width: 130px;
    display: flex;
    flex-direction: column;
   font-family: inherit;
    align-items: center;
    font-size: 16px;
	font-weight: 500;
	background-color: #a5d6a7;
	cursor: pointer;
}

.pokemonTypes span{
	margin:  2px;
	padding: 5px;
	border-radius: 5px;
	display: inline-block;
}

@media (max-width:  600px){
   .card{
    margin: 10px;
   }
}