.container{
	max-width: 1200px;
	margin: 0 auto;
}
.Gridcontainer{
	display: grid;
	grid-template-columns: 2fr 1fr;
}
.Pokcontainer{
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
	justify-items: center;
	grid-gap: 1vw;
	grid-template-rows: 1fr auto;	
}

.LoadBtn{
	display: block;
	margin: 30px 0;
	background-color: #00b248;
	padding: 10px;
	text-align: center;
	border-radius: 10px;
	border: 2px solid #000;
	justify-self: center;
	outline: none;
	font-size: 16px;
	font-weight: 600;
	font-family: inherit;
	cursor: pointer;
}

@media (max-width:  960px){
	.Pokcontainer{
	justify-items: initial;	
 }
}

@media (max-width:  600px){
	.Pokcontainer{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;	
}

.Gridcontainer{
	display: block;  
}
.LoadBtn{
	width: 100%;
}
}